html {
  font-size: 16px;
}
body {
  margin: 0;
  padding: 0;
  background: greenyellow;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 3rem;
  margin: 0 0 1rem;
}

p {
  font-size: 1.5rem;
  line-height: 2rem;
  margin: 0 0 1.5rem;
}

input {
  font-size: 1.5rem;
  background: hsla(0, 0, 100, 0.2);
  border: none;
  padding: 0.25rem 0.5rem;
}

button {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  background: turquoise;
  border: none;
  padding: 0.25rem 1rem;
}

.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

@-webkit-keyframes bringtheboom {
  100% {
    -webkit-transform: scale(3);
            transform: scale(3);
    opacity: 0;
  }
}

@keyframes bringtheboom {
  100% {
    -webkit-transform: scale(3);
            transform: scale(3);
    opacity: 0;
  }
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100vw;
  height: 100vh;
}

.countdown_timer {
  -webkit-animation: bringtheboom 1s ease infinite;
          animation: bringtheboom 1s ease infinite;
}

.target {
  position: absolute;
  border-radius: 50px;
  width: 165px;
  height: 138px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.target img {
  max-width: 100%;
  height: auto;
}

.target_img--hit {
  -webkit-animation: spin 1s linear infinite, bringtheboom 2s ease infinite;
          animation: spin 1s linear infinite, bringtheboom 2s ease infinite;
}

@-webkit-keyframes spin {
  99% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  99% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.stage {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.start {
  max-width: 500px;
  text-align: center;
  color: darkgreen;
}

